import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Drawer from '@material-ui/core/Drawer'
import Button from '@material-ui/core/Button'
import List from '@material-ui/core/List'
import MobileNav from './MobileNav'

const styles = {
  list: {
    width: 250
  },
  fullList: {
    width: 'auto'
  }
}

class AppDrawer extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      top: false
    }
    this.toggleDrawer = this.toggleDrawer.bind(this)
  }

  toggleDrawer() {
    this.setState({
      top: !this.state.top
    })
  }

  render() {
    const { classes } = this.props

    const fullList = (
      <div className={classes.fullList}>
        <List className="column">
          <Button href="#sectionAbout" style={{ fontSize: 18 }}>
            About
          </Button>
          <Button href="#sectionTools" style={{ fontSize: 18 }}>
            Tools
          </Button>
          <Button href="#sectionProjects" style={{ fontSize: 18 }}>
            Projects
          </Button>
          <Button href="#sectionInfo" style={{ fontSize: 18 }}>
            Info
          </Button>
        </List>
      </div>
    )

    return (
      <div className="dn-l z-max absolute fixed w-100 flex justify-end">
        <MobileNav toggleDrawer={this.toggleDrawer} top={this.state.top} />
        <Drawer anchor="top" open={this.state.top} onClose={this.toggleDrawer}>
          <div
            tabIndex={0}
            role="button"
            onClick={this.toggleDrawer}
            onKeyDown={this.toggleDrawer}
            style={{ backgroundColor: '#e8f1f2' }}
          >
            {fullList}
          </div>
        </Drawer>
      </div>
    )
  }
}

AppDrawer.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(AppDrawer)



