import React from 'react'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'

const WebNav = () => {
  return (
    <div className="dn db-l absolute fixed z-2 w-100">
      <Paper
        className="flex items-center justify-between pv3 ph6"
        style={{ backgroundColor: '#e8f1f2' }}
      >
        <a href="#home" className="no-underline">
          <Typography variant="headline">Max Boettcher</Typography>
        </a>
        <div>
          <Button href="#sectionAbout" style={{ fontSize: 18 }}>
            About
          </Button>
          <Button href="#sectionTools" style={{ fontSize: 18 }}>
            Tools
          </Button>
          <Button href="#sectionProjects" style={{ fontSize: 18 }}>
            Projects
          </Button>
          <Button href="#sectionInfo" style={{ fontSize: 18 }}>
            Info
          </Button>
        </div>
      </Paper>
    </div>
  )
}

export default WebNav



