import React from 'react'
import { HamburgerCollapse } from 'react-animated-burgers'

const MobileNav = props => {
  return (
    <div className="z-max absolute top-0 right-0">
      <HamburgerCollapse
        isActive={props.top}
        toggleButton={props.toggleDrawer}
        barColor="#13293d"
        buttonColor="#e8f1f2"
      />
      <div />
    </div>
  )
}

export default MobileNav

