import React from 'react'
import WebProjModal from '../components/WebProjModal'
import MobileProjModal from '../components/MobileProjModal'
import ScrollableAnchor from 'react-scrollable-anchor/lib/ScrollableAnchor'

const SectionProjects = () => {
  return (
    <ScrollableAnchor id={'sectionProjects'}>
      <div
        className="section-wrapper primary-container justify-center"
        style={{ backgroundColor: '#13293d' }}
      >
        <div className="title-about">What I've Done</div>
        <hr className="divider" style={{ backgroundColor: '#e8f1f2' }} />
        <MobileProjModal />
        <WebProjModal />
      </div>
    </ScrollableAnchor>
  )
}

export default SectionProjects



