import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import Button from '@material-ui/core/Button'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Typography from '@material-ui/core/Typography'

class WebProjModal extends React.Component {
  state = {
    open: false,
    scroll: 'body'
  }

  handleClickOpen = scroll => () => {
    this.setState({ open: true, scroll })
  }

  handleClose = () => {
    this.setState({ open: false })
  }

  render() {
    return (
      <div className="dn db-ns">
        <Button disableRipple={true} onClick={this.handleClickOpen('paper')}>
          <div className="box-p">
            <img src="/images/drafty_home.png" alt="drafty home page" />
            <img src="/images/drafty_map.png" alt="draft map page" />
          </div>
        </Button>
        <Dialog
          open={this.state.open}
          onClose={this.handleClose}
          scroll={this.state.scroll}
          aria-labelledby="scroll-dialog-title"
          maxWidth="md"
          fullWidth={true}
        >
          <DialogActions>
            <IconButton onClick={this.handleClose} color="primary">
              <CloseIcon />
            </IconButton>
          </DialogActions>
          <DialogContent
            className="row"
            style={{ padding: 5, alignItems: 'center', flexWrap: 'wrap' }}
          >
            {/* <div className="box-d"> */}
            <div>
              <Typography
                variant="headline"
                style={{ marginLeft: 20, marginBottom: 10 }}
              >
                Drafty
              </Typography>
            </div>
            <div>
              <Typography
                style={{
                  color: '#13293d',
                  marginBottom: 40,
                  marginLeft: 20,
                  marginRight: 20,
                  fontSize: 14
                }}
              >
                Drafty is a React app that I built for my final project at JRS
                Coding School. The app is made for Charleston craft beer
                drinkers and it allows users to search for craft breweries close
                to their current location or to a given street address, using
                the Google Maps API. Brewery data is located in a database that
                I created in CouchDB. Users can also add breweries they like to
                their own favorites list, which is managed using the browser’s
                local storage.
              </Typography>
            </div>
            <div
              className="w-100 flex items-center flex-wrap"
              style={{ justifyContent: 'space-evenly', marginBottom: 30 }}
            >
              <img
                className="shadow-3 ma1"
                src="/images/drafty_home.png"
                alt="drafty home page"
              />
              <img
                className="shadow-3 ma1"
                src="/images/drafty_map.png"
                alt="drafty map page"
              />
              <img
                className="shadow-3 ma1"
                src="/images/drafty_brewery.png"
                alt="drafty brewery page"
              />
              <img
                className="shadow-3 ma1"
                src="/images/drafty_favorites.png"
                alt="drafty favorites page"
              />
            </div>
            <DialogContentText />
          </DialogContent>
        </Dialog>
      </div>
    )
  }
}

export default WebProjModal



