import React from 'react'
import Button from '@material-ui/core/Button'
import ScrollableAnchor from 'react-scrollable-anchor'
import AppDrawer from '../components/AppDrawer'

const SectionAbout = () => {
  return (
    <ScrollableAnchor id={'sectionAbout'}>
      <div
        className="section-wrapper primary-container justify-center"
        style={{ backgroundColor: '#006494' }}
      >
        <div className="title-about">About Me</div>
        <hr className="divider" style={{ backgroundColor: '#e8f1f2' }} />
        <div className="about-paragraph">
          I'm a full-stack software developer living in Charleston, SC and I'm
          always looking for opportunities to challenge myself and improve my
          programming skills. When I'm not coding, I like drinking craft beer,
          shooting pool, and going on the occasional camping trip.
        </div>
        <Button
          href="#sectionTools"
          variant="raised"
          className="no-underline"
          style={{
            marginTop: 20,
            borderRadius: 50,
            padding: 15,
            paddingLeft: 30,
            paddingRight: 30,
            fontSize: 20,
            fontWeight: 'bold',
            backgroundColor: '#e8f1f2'
          }}
        >
          LEARN MORE
        </Button>
      </div>
    </ScrollableAnchor>
  )
}

export default SectionAbout



